@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
html {
  --text-color: #00acdc;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.list__item {
  margin-right: 20px;
}

.list__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--text-color);
}

.is-active {
  color: var(--text-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
